@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Regular.woff) format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Medium.woff) format("woff");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Bold.woff) format("woff");
  font-weight: 900;
  font-style: normal; }

/* @import './_normalize.css'; */
.m-header {
  min-width: 320px;
  min-height: 88px;
  margin: 0 auto;
  border-bottom: 0.5px solid #d9fff5; }

.m-header__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 18px 16px; }

.m-header__list-item {
  width: 40px;
  height: 40px; }

.m-header__list-item--logo::after {
  content: '';
  width: 2px;
  height: 32px;
  background: #eaeaea;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: inline-block;
  margin-left: 10px;
  transform: translateY(10px); }

.m-header__btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #ff3e79; }

.m-header__list-item:first-child::after {
  content: '';
  width: 18px;
  height: 16px;
  background: URL(./static/burger.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: inline-block;
  transform: translate(11px, -33px); }

.m-header__list-item:nth-child(3)::after {
  content: '';
  width: 20px;
  height: 20px;
  background: URL(./static/tools.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: inline-block;
  transform: translate(10px, -35px); }

.m-header__list-item:last-child::after {
  content: '';
  width: 20px;
  height: 20px;
  background: URL(./static/search.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: inline-block;
  transform: translate(10px, -35px); }

.m-header__list-item:hover {
  transform: scale(0.95);
  transition: 0.3s;
  /*  box-shadow: 0px 0px 12px 0px rgba(34, 60, 80, 0.15); */ }

.m-header__list-item:active,
.m-header__btn:focus {
  transform: scale(1.25);
  transition: 0.3s; }

.main {
  min-height: 448px; }
  .main__title {
    color: #1b1c21;
    font-size: 28px;
    font-weight: 900;
    padding: 24px 16px 24px 0; }
    .main__title::before {
      content: '';
      width: 4px;
      height: 40px;
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
      background: #41f6d7;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      display: inline-block;
      transform: translateY(10px);
      margin-right: 12px; }
  .main__navigation {
    max-height: 40px;
    padding: 0 16px 24px 16px;
    overflow: auto;
    overflow-y: hidden;
    white-space: nowrap; }
    .main__navigation-list {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: flex-start;
      align-items: center; }
    .main__navigation-item {
      padding-right: 5px;
      padding-left: 5px; }
      .main__navigation-item:hover {
        border: 1px solid #b8ffec;
        border-radius: 5px; }
    .main__navigation-link {
      display: block;
      text-decoration: none;
      color: #7e7e82;
      padding: 5px;
      font-size: 16px;
      white-space: nowrap; }
  .main__text, .main__text--hidden {
    font-size: 13px;
    font-weight: 400;
    color: #1b1c21;
    padding-left: 16px;
    padding-bottom: 16px;
    word-spacing: normal; }
  .main__btn-more {
    font-size: 16px;
    font-weight: 500;
    padding-left: 8px;
    padding-bottom: 16px; }
    .main__btn-more::before {
      content: '';
      width: 8px;
      height: 10px;
      background: URL(./static/arrows.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      display: inline-block;
      margin-right: 15px;
      transition: 0.3s; }
  .main .arrows__up::before {
    content: '';
    width: 8px;
    height: 10px;
    transform: rotate(180deg);
    background: URL(./static/arrows.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    display: inline-block;
    margin-right: 15px;
    transition: 0.3s; }
  .main__cps-pic {
    display: block; }

.brands__header {
  margin-bottom: 16px; }

.brands__list {
  padding-bottom: 46px; }

.brands__item {
  display: flex;
  justify-content: flex-start;
  background: url(./static/go.svg) no-repeat 90% 50%;
  color: #1b1c21;
  width: 240px;
  height: 72px;
  padding: 16px;
  font-size: 16px;
  font-weight: 500;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  transition: transform 0.3s, box-shadow 0.3s; }
  .brands__item-type {
    max-width: 70%;
    max-height: 40px; }
  .brands__item:hover {
    transform: scale(0.95);
    box-shadow: 0px 0px 12px 0px rgba(34, 60, 80, 0.15); }
  .brands__item:active {
    border: 1px solid #ff3e79;
    background-position: 95% 50%; }

.swiper-pagination-bullet {
  opacity: 1;
  background-color: #dddddd;
  width: 12px;
  height: 12px;
  transition: transform 0.3s, background-color 0.3s; }
  .swiper-pagination-bullet:active, .swiper-pagination-bullet:focus {
    background-color: #b5b6bc;
    transform: scale(1.3); }

.types-of {
  min-height: 308px;
  min-width: 320px;
  border-top: 1px solid #d9fff5; }
  .types-of__header {
    font-size: 16px;
    text-transform: uppercase;
    color: #7e7e82;
    padding: 16px;
    background-color: #fcfbfa;
    margin-bottom: 16px; }
  .types-of__swiper {
    min-height: 228px; }
  .types-of__item {
    display: flex;
    justify-content: space-between;
    color: #1b1c21;
    width: 240px;
    height: 160px;
    padding: 16px;
    font-size: 16px;
    font-weight: 500;
    border: 1px solid #eaeaea;
    border-radius: 6px; }
    .types-of__item:hover {
      transform: scale(0.95);
      box-shadow: 0px 0px 12px 0px rgba(34, 60, 80, 0.15); }
    .types-of__item:active {
      border: 1px solid #ff3e79;
      background-position: 95% 50%; }
    .types-of__item__img {
      background: url(./static/go.svg);
      width: 40px;
      height: 40px;
      align-self: flex-end;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain; }

.swiper-pagination-bullet {
  opacity: 1;
  background-color: #dddddd;
  width: 12px;
  height: 12px; }
  .swiper-pagination-bullet-active {
    background-color: #b5b6bc;
    transform: scale(1.3); }

.types-of__img {
  background: url(./static/go.svg);
  width: 40px;
  height: 40px;
  align-self: flex-end;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain; }

.title {
  min-width: 320px;
  border-top: 1px solid #d9fff5; }

.title__header {
  font-size: 16px;
  text-transform: uppercase;
  color: #7e7e82;
  padding: 16px;
  background-color: #fcfbfa; }

.swiper-wrapper {
  margin-left: 16px; }

.prices__header {
  margin-bottom: 16px; }

.prices__swiper {
  padding-bottom: 60px; }

.prices__item {
  display: grid;
  max-width: 260px;
  height: 200px;
  padding: 16px;
  font-size: 14px;
  font-weight: 400;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  position: relative; }

.prices__item:hover {
  transform: scale(0.95);
  box-shadow: 0px 0px 12px 0px rgba(34, 60, 80, 0.15); }

.prices__item-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  max-height: 56px; }

.prices__item-text > span {
  color: #7E7E82;
  font-size: 14px; }

.prices__order-btn {
  position: absolute;
  right: 16px;
  bottom: 26px;
  justify-self: flex-start;
  width: 119px;
  height: 32px;
  background-color: #ff3e79;
  border-radius: 10px;
  color: #eee;
  font-size: 12px;
  white-space: pre; }
  .prices__order-btn:active {
    transform: scale(1.1);
    transition: 0.3s; }

.prices__description {
  min-height: 193px;
  min-width: 320px;
  font-size: 14px;
  padding: 16px; }

.prices__get-offer {
  padding: 16px 0;
  font-weight: 500;
  font-size: 16px;
  text-align: left; }

.prices__get-offer:after {
  content: '';
  display: inline-block;
  width: 12px;
  height: 18px;
  background: URL(./static/side-arrow.svg) center center/cover no-repeat;
  vertical-align: middle;
  margin-left: 5px; }

.footer {
  color: #7E7E82;
  font-size: 14px;
  text-align: center;
  white-space: pre-wrap;
  border-top: 1px solid #D9FFF5; }
  .footer__paddings {
    color: #7E7E82;
    display: block;
    padding-top: 16px; }

body {
  box-sizing: border-box;
  font-family: 'TT Lakes', serif, sans-serif; }

.hidden {
  display: none; }
